@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a{
  text-decoration: none;
}

body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1200px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  color: lightgray;
}

.App{padding:20px;text-align:left}.win{background-color:#4da376}.loss{background-color:#a44d5d}
.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-gap:10px;gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name-section .team-name{font-size:3rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:center;justify-self:center}.TeamPage .more-link :hover{color:#fdfafa}
.MatchDetailCard{padding:.9rem;display:grid;grid-template-columns:2fr 1fr;margin-bottom:1rem}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px;font-size:1rem;font-weight:lighter}.MatchDetailCard .detail-section-2{text-align:right;font-size:.8rem}.MatchDetailCard .detail-section-2 h3:not(:first-child){margin-top:15px}.MatchDetailCard .detail-section-2 h3{font-weight:590}
.MatchSmallCard{padding:.9rem}.MatchSmallCard .match-result{margin-top:10px;font-size:.8rem;font-weight:lighter}
.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}
.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-gap:20px;gap:20px}.MatchPage .page-heading{margin:20px auto}.MatchPage .year-selector{justify-self:end;text-align:right;padding:20px}
.TeamCard{text-align:center;padding:20px;background-color:rgba(70,69,69,.295);height:170px;border:1px solid #f5f5f5}
.HomePage .team-tiles-section{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:10px;gap:10px;margin-top:2rem}.HomePage .header-section h1{font-size:3rem}
